  border-inline-start: 14px solid rgb(var(--light-rgb));
  border-inline-end: 0 solid rgb(var(--light-rgb));
  border-block-end: 14px solid transparent;
  content: " ";
}
.vtimeline .timeline-wrapper:after {
  content: " ";
  display: table;
  clear: both;
}
